import React from "react";
import { connect } from "react-redux";
import { Helmet } from 'react-helmet';
import { Card, CardBody, CardHeader, CardTitle, Row, Col } from "reactstrap"
import Breadcrumbs from "../../../../components/@vuexy/breadCrumbs/BreadCrumb"
import UsersTable from './Tables/UsersTable';
import UsersModal from './Modals/UsersModal';

import * as API from "../../../../utility/crud/app.crud";
import * as _Date from "../../../../utility/dateAndTime";
import { toastr } from 'react-redux-toastr'

class Users extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            columns: [],
            data: [],
            filteredData: [],
            groupsdata: [],
            checkedgroups: [],
            groupoptions: [],
            usergroupsdata: [],
            value: '',
            show: false,
            showdelete: false,
            popoverOpen: false,

            USER_ID: '',
            USER_NAME: '',
            EMAIL: '',
            PASSWORD_HASH:'',
            FIRST_NAME: '',
            locationid: '',
            locationname: '',

            CREATEDBY: props.user.EMAIL,
            MODIFIEDBY: props.user.EMAIL,
            CREATED_DATE: _Date.ShowDate(),
            MODIFIED_DATE: _Date.ShowDate(),
            InsertUpdate: '',

            opensearch: false,

            searchcolumns: [],
            searchdata: [],
            searchFilteredData: [],
            searchvalue: '',

        }

        this.handleShow = () => {
            this.setState({
                groupoptions: this.state.groupsdata,
                show: true,
                InsertUpdate: 'Insert'
            });
            this.clearData();
        }

        this.handleHide = () => {
            this.setState({
                show: false,
                groupoptions: [],
                checkedgroups: [],
            });
        }

        this.handleHideDelete = () => {
            this.setState({
                showdelete: false
            });
        }

        this.handleShowSearch = () => {
            this.setState({ opensearch: true });
        };

        this.handleHideSearch = () => {
            this.setState({ opensearch: false });
        };

        this.populateData();

    }

    populateData() {
        const getdata = `
            query{
                appUsers {
                    USER_ID
                    USER_NAME
                    EMAIL
                    FIRST_NAME
                    USER_GROUPS {
                        GROUP {
                            GROUP_ID
                            GROUP_NAME
                        }
                    }
                    CREATED_DATE
                    CREATEDBY
                    MODIFIED_DATE
                    MODIFIEDBY
                },

                Groups {
                    GROUP_ID
                    GROUP_NAME
                },

                userAssignedGroups {
                    USER_GROUP_ID
                    USER_ID
                    GROUP_ID
                    GROUP {
                        GROUP_ID
                        GROUP_NAME
                    }
                }
            }
        `;

        API.getData(getdata)
            .then(({ data }) => {
                const Groups = data.data.Groups;
                
                let groupsdata = [];

                Groups.forEach(group => {
                    groupsdata.push({ 
                        label: group.GROUP_NAME, 
                        value: group.GROUP_ID
                    })
                });
                
                this.setState({
                    data: data.data.appUsers,
                    usergroupsdata: data.data.userAssignedGroups,
                    groupsdata
                })
            }).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to USER Load Data');
                }
            })

    }

    handleSubmit = async (event) => {
        event.preventDefault();
        
        let userDetails = {}

        const userExistsQuery = `
            query {
                userExists (
                    USER_NAME: "${this.state.USER_NAME}",
                    EMAIL: "${this.state.EMAIL}"
                ){
                    USER_ID
                }
            }
        `;

        const response = await API.getData(userExistsQuery);

        if (response.data.data && response.data.data.userExists){            
            userDetails = response.data.data.userExists;
        }

        if (this.state.InsertUpdate === 'Insert' && Object.keys(userDetails).length === 0) {

            const addAppUserQuery = `
                mutation {
                    addAppUser (
                        USER_NAME: "${this.state.USER_NAME}",
                        PASSWORD_HASH: "${this.state.PASSWORD_HASH}",
                        EMAIL: "${this.state.EMAIL}",
                        FIRST_NAME: "${this.state.FIRST_NAME}",
                        CREATED_DATE: "${this.state.CREATED_DATE}",
                        MODIFIED_DATE: "${this.state.MODIFIED_DATE}",
                        CREATEDBY: "${this.state.CREATEDBY}",
                        MODIFIEDBY: "${this.state.MODIFIEDBY}",
                    ){
                        USER_ID
                    }
                }
            `;
            

            API.create(addAppUserQuery)
                .then(({data}) => {

                    const USER_ID = data.data.addAppUser.USER_ID

                    const userGroups = [];

                    this.state.checkedgroups.forEach(group => {
                        userGroups.push(
                            `
                                {
                                    USER_ID: ${ USER_ID },
                                    GROUP_ID: ${ group.value }, 
                                    CREATED_DATE: "${ this.state.CREATED_DATE }",
                                    MODIFIED_DATE: "${ this.state.MODIFIED_DATE }",
                                    CREATED_BY: "${ this.state.CREATEDBY }", 
                                    MODIFIED_BY: "${ this.state.MODIFIEDBY }"
                                }
                            `
                        )
                    })

                    const addUserGroupQuery = `
                            mutation {
                                userAssignedGroupBatchInsert (user_groups: [${ userGroups }]) {                                    
                                    USER_GROUP_ID
                                }
                            }
                        `;
                        
                    API.create(addUserGroupQuery)
                        .then(() => {
                            this.clearData();
                            this.handleHide();
                            this.populateData();
                            toastr.success('Create Record', 'Record Created Successfully');
                        }).catch(error => {
                            if (error.response || error.request) {
                                toastr.error('Error Loading Data', 'Failed to Load Data');
                            }
                        })

                }).catch(error => {
                    if (error.response || error.request) {
                        toastr.error('Error Loading Data', 'Failed to Load Data');
                    }
                })
        } else if (this.state.InsertUpdate === 'Update' &&  Object.keys(userDetails).length !== 0 && userDetails.USER_ID === this.state.USER_ID) {

            const USER_ID = this.state.USER_ID;

            let insertUserGroups = [];
            let deleteUserGroups = [];
            let userGroups = [];
            let userGroupsIds = [];
            let checkedGroupIds = [];

            this.state.usergroupsdata.forEach(group => {
                if(group.USER_ID === USER_ID) {
                    userGroups.push(group)
                    userGroupsIds.push(group.GROUP.GROUP_ID)
                }
            });

            this.state.checkedgroups.forEach(group => {
                checkedGroupIds.push(group.value)
            })

            const addedUserGroups = this.state.checkedgroups.filter(group => !userGroupsIds.includes(group.value))
            const removedUserGroups = userGroupsIds.filter(groupid => !checkedGroupIds.includes(groupid))

            addedUserGroups.forEach(group => {
                insertUserGroups.push(
                    `
                        {
                            USER_ID: ${ USER_ID },
                            GROUP_ID: ${ group.value }, 
                            CREATED_DATE: "${ this.state.CREATED_DATE }",
                            MODIFIED_DATE: "${ this.state.MODIFIED_DATE }",
                            CREATED_BY: "${ this.state.CREATEDBY }", 
                            MODIFIED_BY: "${ this.state.MODIFIEDBY }"
                        }
                    `
                )
            })

            removedUserGroups.forEach(groupid => {
                const userGroupId = userGroups.filter(usergroup => usergroup.GROUP.GROUP_ID === groupid)[0].USER_GROUP_ID;
                deleteUserGroups.push(
                    `
                        {
                            USER_GROUP_ID: ${ userGroupId },
                        }
                    `
                )
            })

            const updateAppUserQuery = `
                mutation{
                    updateAppUser (
                        USER_ID: ${USER_ID},
                        USER_NAME: "${this.state.USER_NAME}",
                        PASSWORD_HASH: "${this.state.PASSWORD_HASH}",
                        EMAIL: "${this.state.EMAIL}",
                        FIRST_NAME: "${this.state.FIRST_NAME}",
                        MODIFIED_DATE: "${this.state.MODIFIED_DATE}",
                        MODIFIEDBY: "${this.state.MODIFIEDBY}"
                    ){
                        USER_ID
                    }
                    
                    userAssignedGroupBatchInsert (user_groups: [${ insertUserGroups }]) {                                    
                        USER_GROUP_ID
                    }

                    userAssignedGroupBatchDelete (user_groups: [${ deleteUserGroups }]) {                                    
                        USER_GROUP_ID
                    }
                }
            `;
            console.log('Update User', updateAppUserQuery);

            API.update(updateAppUserQuery)
                .then(() => {
                    this.clearData();
                    this.handleHide();
                    this.populateData();
                    toastr.info('Update Record', 'Record Updated Successfully');
                }).catch(error => {
                    if (error.response || error.request) {
                        toastr.error('Error Updating Data', 'Failed to Update Data');
                    }
                })
        } else {
            toastr.error('Not Allowed', 'Email or Username already EXIST');
        }

    }

    handleDelete = (event) => {
        event.preventDefault();

        const formDelete = `
            mutation{
                deleteAppUser(
                    USER_ID:${this.state.USER_ID}
                    ){
                    USER_ID
                }
            }
        `;
        
        API.remove(formDelete)
            .then((r) => {
                this.handleHideDelete();
                this.populateData();
                toastr.warning('Delete Record', 'Record Deleted Successfully');
            }).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Data');
                }
            })

    }

    handleEditData = (e) => {

        const userGroups = [];
        
        this.state.usergroupsdata.forEach(group => {
            if(group.USER_ID === e.USER_ID) {
                userGroups.push({ 
                    label: group.GROUP.GROUP_NAME, 
                    value: group.GROUP.GROUP_ID
                })
            }
        });

        this.setState({
            USER_ID: e.USER_ID,
            EMAIL: e.EMAIL,
            FIRST_NAME: e.FIRST_NAME,
            USER_NAME: e.USER_NAME,
            groupoptions: this.state.groupsdata,
            checkedgroups: userGroups,
            show: true,
            InsertUpdate: 'Update'
        })
    }

    handleDeleteData = (e) => {
        this.setState({
            USER_ID: e.USER_ID,
            FIRST_NAME: e.FIRST_NAME,
            USER_NAME: e.USER_NAME,
            showdelete: true,
            InsertUpdate: 'Delete'
        })
    }

    handleShowUserGroups = e => {
        //console.log(e)
        this.setState({
            popoverOpen: !this.state.popoverOpen,
        })
    }

    clearData() {
        this.setState({
            USER_ID: '',
            USER_NAME: '',
            EMAIL: '',
            FIRST_NAME: '',
            PASSWORD_HASH:'',
        })
    }

    handleCloseSearch = (e) => {
        this.setState({
            locationid: e.locationid,
            locationname: e.locationname,
            opensearch: false,
        });
    };

    handleFilter = (e) => {
        let value = e.target.value
        let data = this.state.data
        let filteredData = this.state.filteredData
        this.setState({ value });

        if (value.length) {
            filteredData = data.filter(item => {
                let startsWithCondition =
                    item.FIRST_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.EMAIL.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.USER_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.FIRST_NAME.toString().toLowerCase().includes(value.toLowerCase()) ||
                    item.EMAIL.toString().toLowerCase().includes(value.toLowerCase()) ||
                    item.USER_NAME.toString().toLowerCase().includes(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredData })
        }
    }

    handleSearchFilter = (e) => {
        let value = e.target.value
        let data = this.state.searchdata
        let searchFilteredData = this.state.searchFilteredData
        this.setState({ searchvalue: value });

        if (value.length) {
            searchFilteredData = data.filter(item => {
                let startsWithCondition = item.locationname.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition = item.locationname.toString().toLowerCase().includes(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ searchFilteredData })
        }
    }

    handleGroupChange = (checkedgroups) => {
        this.setState({
            checkedgroups: checkedgroups ? checkedgroups : []
        });
    }

    render() {
        const { data, value, filteredData } = this.state
        return (
            <>
                <Helmet><title>Users</title></Helmet>

                <React.Fragment>
                    <Breadcrumbs
                        breadCrumbTitle="Security"
                        breadCrumbParent="Home"
                        breadCrumbActive="Users"
                    />
                    <Row>
                        <Col sm="12">
                            <Card>
                                <CardHeader>
                                    <CardTitle>Users</CardTitle>
                                </CardHeader>
                                <CardBody className="rdt_Wrapper">

                                    <UsersTable
                                        role="Users"
                                        handleEdit={this.handleEditData}
                                        handleDelete={this.handleDeleteData}
                                        handleShowUserGroups={this.handleShowUserGroups}
                                        value={value}
                                        data={data}
                                        filteredData={filteredData}
                                        handleShow={this.handleShow}
                                        handleFilter={this.handleFilter}
                                        popoverOpen={this.state.popoverOpen}
                                    />

                                    <UsersModal
                                        show={this.state.show}
                                        handleHide={this.handleHide}
                                        InsertUpdate={this.state.InsertUpdate}
                                        handleSubmit={this.handleSubmit}
                                        handleShowSearch={this.handleShowSearch}

                                        groupOptions={this.state.groupoptions}
                                        checkedGroups={this.state.checkedgroups}
                                        handleGroupChange={this.handleGroupChange}

                                        first_name={this.state.FIRST_NAME}
                                        changefirst_name={e => this.setState({FIRST_NAME: e.target.value })}

                                        user_name={this.state.USER_NAME}
                                        changeuser_name={e => this.setState({USER_NAME: e.target.value })}
                                        
                                        Email={this.state.EMAIL}
                                        changeEmail={e => this.setState({EMAIL: e.target.value })}

                                        changePassword={e => this.setState({PASSWORD_HASH: e.target.value })}

                                        showdelete={this.state.showdelete}
                                        handleHideDelete={this.handleHideDelete}
                                        handleDelete={this.handleDelete}
                                    />


                                </CardBody>
                            </Card>
                        </Col>
                    </Row>
                </React.Fragment>

            </>

        )
    }
}

const mapStateToProps = ({ auth: { user } }) => ({
    user
});

export default (connect(mapStateToProps)(Users));